<template>
  <div class="home_container">
    <van-nav-bar title="资讯头条" fixed />

    <!-- 导入注册 -->
    <!-- 在使用组件的时候，如果某个属性名是小驼峰的格式，则在绑定属性的时候，建议改成连字符 cmt-count的格式 -->
    
    <van-pull-refresh v-model="isLoading" @refresh="onRefresh" :disabled="finished">
    <van-list
    v-model="loading"
    :finished="finished"
    finished-text="没有更多了"
    @load="onLoad"
>
    <ArticleInfo v-for="item in artList" :key="item.id" :title="item.title" :author="item.aut_name" :cmtCount="item.comm_count" :time="item.pubdate" :cover="item.cover"></ArticleInfo>
  </van-list>
</van-pull-refresh>
  </div>
</template>

<script>
// 按需导入 API 接口
import { getArticleListApi } from '@/API/articleApi.js'
// 导入 ArticleInfo组件
import ArticleInfo from '@/components/Article/ArticleInfo.vue';
export default {
  name: 'Home',
  created() {
    this.initArticleList()

  },
  data () {
    return {
      // 页码值
      page: 1,
      //  每页显示限制
      limit: 10,
      artList: [],
      // 是否正在加载下一页的数据
      loading:true,
      // 所有数据是否已经加载完毕了
      finished:false,
      // 下拉刷新，是否加载完成
      isLoading:false

    }
  },
  methods: {
    // 封装获取文章列表数据的方法
    async initArticleList(isRefresh) {
      // 发起GET请求，获取数据
      const{data:res} = await getArticleListApi(this.page,this.limit)
      if(isRefresh) {
        // 下拉刷新，新数据在前，旧数据在后
        this.artList = [...res,...this.artList]
        this.isLoading = false
         // // 如果这次返回的res没有数据了，把finished改为TRUE
        if(res.length === 0){
          this.finished = true
        }

      } else {
          // 上拉加载更多，旧数据在前，新数据在后
          // // 新旧数据的拼接
        this.artList = [...this.artList,...res]
        this.loading = false
        // // 如果这次返回的res没有数据了，把finished改为TRUE
        if(res.length === 0){
          this.finished = true
        }
      }
    },
    // 加载下一页的方法
    onLoad() {
      this.page++
      this.initArticleList()
    },
    onRefresh() {
     this,this.page++
     this.initArticleList(true)
    }
    
  },
  // 注册组件
  components: {
    ArticleInfo
  }
}
</script>

<style lang="less">
.home_container {
  padding: 46px 0 50px 0;
  .van-nav-bar {
    background-color: #007bff;
  }
  .van-nav-bar__title {
    color: rgb(255, 255, 255);
  }
}
</style>
